*,
::before,
::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

body {
  color: #21242E;
  background-color: #ffffff;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.03em;
  font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

button {
  border: none;
  background-color: transparent;
  cursor: url("/dist/images/cursor.svg"), auto;
}

.container {
  width: 100%;
  min-width: 320px;
  padding-left: 16px;
  padding-right: 16px;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}
a {
  text-decoration: none;
  cursor: url("/dist/images/cursor.svg"), auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

ol,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.header {
  padding: 16px 0;
  border-bottom: 1px solid #e4e4e4;
  width: 100%;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.logo {
  width: 91px;
  height: 32px;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
}

.header__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #D0BC7A;
  transition: all 200ms linear;
}

.header__link:hover,
.header__link:focus {
  color: #21242E;
}

@media screen and (min-width: 768px) {
  .header__link--text {
    margin-right: 32px;
  }
}

.header__link svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: none;
  stroke: currentColor;
}

.header__link .user {
  fill: currentColor;
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  fill: currentColor;
}

@media screen and (max-width: 767.99px) {
  .header__link--text {
    display: none;
  }
}

.header__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #21242E;
  width: 34px;
  height: 34px;
  margin-left: 16px;
  transition: all 200ms linear;
}
@media screen and (min-width: 768px) {
  .header__btn {
    margin-left: 32px;
  }
}

.header__btn:hover,
.header__btn:focus {
  color: #D0BC7A;
}

.header__btn svg {
  width: 32px;
  height: 32px;
  fill: currentColor;
}